<template>
  <div id="app">
    <div class="container">
      <div class="header">
        <div class="logo">顾客点餐</div>
      </div>
      <div class="view-part">
        <div class="side-bar">
          <div class="title1">菜单分类</div>
          <ul>
            <li v-for="item in dishTypes" v-bind:key="item.id">
              <a v-bind:href="'#' + item.id">{{item.name}}</a>
            </li>
          </ul>
          <div class="title2" v-on:click="showOrder">{{ orderState ? '查看已选' : '返回点菜' }}<span style="color: red">{{ ' ' + totalCount + ' | ¥' + totalPrice}}</span></div>
        </div>
        <div class="menu-part"  v-if="orderState">
          <div class="menu-part-inside" v-for="item in dishes" v-bind:key="item.type.id" style="display: flex">
            <div class="title" v-bind:id="item.type.id">{{ item.type.name }}</div>
            <div class="item" v-for="subItem in item.dishList" v-bind:key="subItem.id">
              <div class="pic"></div>
              <div class="name">{{ subItem.name }}<div class="recommend" v-if="subItem.recommend">推荐</div></div>
              <div class="detail">{{ subItem.detail }}</div>
              <div class="footer">
                <div class="price">
                  <div class="old-price">¥{{ subItem.oldPrice }}</div>
                  <div class="new-price">¥{{ subItem.newPrice }} <div class="unit">/{{ subItem.unit }}</div></div>
                </div>
                <div class="btn">
                  <button v-on:click="choose(subItem)">选择</button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="order-page" v-else>
          <div class="title">已经选择</div>
          <div class="list">
            <ul>
              <li v-for="item in order" v-bind:key="item">
                <div class="dishItem">
                  <div class="pic"></div>
                  <div class="name">{{item.name}}</div>
                  <div class="price">¥{{item.newPrice}}</div>
                  <button v-on:click="deleteItem(item.id)">删除</button>
                </div>
              </li>
            </ul>
          </div>
          <div class="footer">
            <div class="msg">共计：{{totalPrice}}元</div>
            <button v-on:click="postOrder" v-bind:disabled="postMethod">支付</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'App',
  components: {},
  data: function () {
    return {
      dishTypes: [],
      dishes: [],
      order: [],
      orderState: true,
      totalCount: 0,
      totalPrice: 0.0,
      postMethod: false
    }
  },
  created() {
    axios({
      method: 'get',
      url: 'http://localhost:8080/customer/menu'
    }).then((response) => {
      // console.log(response);
      this.dishTypes = response.data[0].dishType;
      this.dishes = response.data[1].dishes;
      // console.log(this.dishTypes);
      // console.log(this.dishes);
    });
  },
  methods: {
    choose(item) {
      this.order.push(item);
      this.totalCount = this.order.length;
      this.totalPrice = Number(this.totalPrice) + Number(item.newPrice);
      this.totalPrice = this.totalPrice.toFixed(2);
    },
    showOrder() {
      this.orderState = !this.orderState;
    },
    deleteItem(id) {
      let i = 0;
      for (i = 0; i < this.order.length; ++i) {
        if (this.order[i].id === id) break;
      }
      this.totalCount = this.totalCount - 1;
      this.totalPrice = Number(this.totalPrice) - Number(this.order[i].newPrice);
      this.totalPrice = this.totalPrice.toFixed(2);
      if (this.totalPrice <= 0.0) this.totalPrice = 0.0;
      this.order.splice(i, 1);
    },
    postOrder() {
      this.postMethod = true;
      let postData = {
        order: this.order,
        totalPrice: this.totalPrice
      };
      // console.log(postData);
      // console.log(JSON.stringify(postData));
      axios({
        method: 'post',
        url: 'http://localhost:8080/customer/order',
        headers : {
          'Content-Type' : 'application/json;charset=utf-8'
        },
        data: JSON.stringify(postData)
      }).then(res=>{
        console.log(res);
        alert("支付成功");
        this.order = [];
        this.totalCount = 0;
        this.totalPrice = 0.0;
        this.postMethod = false;
      }).catch(err=>{
        console.log(err);
        alert("网络存在问题，请再试一次")
        this.postMethod = false;
      });
    }
  }
}
</script>

